$accent-color: #f00;
$descr-color: #19cbfa;

.calendar {
  display: grid;
  grid-template-columns: repeat(7, minmax(120px, auto));
  grid-column-gap: 35px;
  padding-top: 20px;
}

@for $i from 0 through 6 {
  .calendar__slot_day_#{$i} { grid-column: if($i != 0, $i, 7); }
}

$rows: ('label', 'morning', 'day', 'evening');
@for $i from 1 through length($rows) {
  .calendar__slot_#{nth($rows, $i)} {grid-row: $i;}
}

.calendar__dow_title {
  font-weight: normal;
  font-size: 1em;
}

.calendar__date {
  color: $accent-color;
  font-weight: bold;
}

.calendar__event {
  border-top: 1px solid black;
  padding-left: 15px;
  position: relative;
  margin-bottom: 35px;
}

.calendar__event:after {
  padding: 0;
  margin: 0;
  display: block;
  content: '';
  height: 79px;
  width: 1px;
  border-left: 1px solid black;
  position: absolute;
  top: -1px;
  left: -1px;
}

.calendar__slot_morning .calendar__event:first-child {
  border-top: 2px solid $accent-color;
}

.calendar__slot_morning .calendar__event:first-child:after {
  border-left: 2px solid $accent-color;
  top: -2px;
}

.calendar__event-description-title,
.calendar__event-title {
  font-weight: bold;
  font-size: 1em;
}

.calendar__event-title {
  font-weight: bold;
  font-size: 1em;
  height: 6em;
  overflow: hidden;
  line-height: 1.4;
  margin-top: 10px;
  cursor: pointer;
}

.calendar__event-title:hover {
  color: $descr-color;
}

.calendar__event-time {
  position: absolute;
  top: 0px;
  left: 0px;
  transform: rotate(-90deg) translate(-13px, -53px);
}

.calendar__event-description_hidden {
  display: none;
}

.calendar__event-description {
  position: absolute;
  background-color: $descr-color;
  width: 200px;
  top: -2px;
  left: -1px;
  z-index: 2;
  padding: 15px 20px;
  margin-bottom: 10px;
}

.calendar__event-description-title {
  color: white;
  margin-top: 5px;
}

.calendar__event-time_em {
  color: $descr-color;
}

.calendar__event-description-text {
  font-size: .9em;
  line-height: 1.6;
}

.calendar__close-event-descr-btn {
  color: white;
  font-weight: bold;
  font-size: 1.3em;
  position: absolute;
  right: 8px;
  top: 4px;
  cursor: pointer;
}

.calendar__week-switcher > * {
  display: inline-block;
}

.calendar__week-title {
  margin: 0;
  font-weight: bold;
  color: $accent-color;
  margin-left: 10px;
}

.calendar__week-switcher-button {
  border: none;
  outline: none;
  cursor: pointer;

  width: 30px;
  height: 30px;

  text-align: center;
  font-size: 1.4em;
  color: black;
  background-color: rgba(0,0,0,0);
}

.calendar__week-switcher-button:hover {
  background-color: lightgrey;
}

.google-cal-link a {
  color: #555555;
}

.google-cal-link a:visited {
  color: #858585;
}

.fb-link {
  color: white;
  font-weight: bold;
  padding-left: 4px;
}

.loading-error, .epic-fail-error {
  font-size: 1.2em;
  color: grey;
}


@media (max-width: 480px) {
  .calendar {
    grid-template-columns: minmax(120px, auto);
  }

  $rows: ('label', 'morning', 'day', 'evening');
  @for $i from 1 through length($rows) {
    @for $j from 0 through 6 {
      .calendar__slot_day_#{$j}.calendar__slot_#{nth($rows, $i)} {
        grid-column: 1;
        grid-row: if($j != 0, $j, 7) * 4 + $i;
      }
    }
  }

  .calendar__event-title {
    height: 4em;
  }

  .calendar__week-switcher {
    position: sticky;
    left: 200px;
    top: 0px;
    padding-top: 15px;
    padding-bottom: 5px;
    background-color: white;
    z-index: 5;
    transform: translate(-60px, 0px);
    width: calc(100vw - 50px);
    padding-left: 30px;
  }

  .calendar__event-description {
    width: calc(100% - 20px);
  }
}
