  .calendar__slot_day_1.calendar__slot_morning {
    grid-column: 1;
    grid-row: 6; }
  .calendar__slot_day_2.calendar__slot_morning {
    grid-column: 1;
    grid-row: 10; }
  .calendar__slot_day_3.calendar__slot_morning {
    grid-column: 1;
    grid-row: 14; }
  .calendar__slot_day_4.calendar__slot_morning {
    grid-column: 1;
    grid-row: 18; }
  .calendar__slot_day_5.calendar__slot_morning {
    grid-column: 1;
    grid-row: 22; }
  .calendar__slot_day_6.calendar__slot_morning {
    grid-column: 1;
    grid-row: 26; }
  .calendar__slot_day_0.calendar__slot_day {
    grid-column: 1;
    grid-row: 31; }
  .calendar__slot_day_1.calendar__slot_day {
    grid-column: 1;
    grid-row: 7; }
  .calendar__slot_day_2.calendar__slot_day {
    grid-column: 1;
    grid-row: 11; }
  .calendar__slot_day_3.calendar__slot_day {
    grid-column: 1;
    grid-row: 15; }
  .calendar__slot_day_4.calendar__slot_day {
    grid-column: 1;
    grid-row: 19; }
  .calendar__slot_day_5.calendar__slot_day {
    grid-column: 1;
    grid-row: 23; }
  .calendar__slot_day_6.calendar__slot_day {
