  .calendar__slot_day_4.calendar__slot_evening {
    grid-column: 1;
    grid-row: 20; }
  .calendar__slot_day_5.calendar__slot_evening {
    grid-column: 1;
    grid-row: 24; }
  .calendar__slot_day_6.calendar__slot_evening {
