  .calendar__event-title {
    height: 4em; }
  .calendar__week-switcher {
    position: -webkit-sticky;
    position: sticky;
    left: 200px;
    top: 0px;
    padding-top: 15px;
    padding-bottom: 5px;
    background-color: white;
    z-index: 5;
    -webkit-transform: translate(-60px, 0px);
            transform: translate(-60px, 0px);
    width: calc(100vw - 50px);
    padding-left: 30px; }
  .calendar__event-description {
    width: calc(100% - 20px); } }

